.App {
  text-align: center;
}

.home-bg {
  background-image: url("/images/home-bg.png");
  background-repeat: repeat;
}

.loading {
  position: fixed;
  z-index: 2001;
  height: 100%;
  width: 100%;
  overflow: visible;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  color: #fff;
  background-color:  #333;
  opacity: .4;
  text-align: center;
  padding-top: 6rem;
}
.no-loading { display: none; } 


.popup-on {
  position: fixed;
  z-index: 1110;
  height: 100%;
  width: 100%;
  overflow: visible;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  color: #fff;
  background-color:  #333;
  opacity: .7;
  text-align: center;
}
.popup-body {
  position: fixed;
  z-index: 1111;
  height: 100%;
  width: 100%;
  overflow: visible;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color:  transparent;
  text-align: center;
  padding-top:6rem;
}
.popup-off { display: none;} 

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #282c34;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.border-circle { border-radius: 1rem !important; }

.header-bg { background-image: url(/images/banner-bg.png); opacity: 0.8; background-size: 'cover'}

.cursor-pointer { cursor: pointer }
.cursor-pointer:hover { color: red; }

.copyable:hover { color: red; cursor: context-menu;  }

.hover-shadow:hover {
  box-shadow: 0 3px 8px 0 rgba(200,200,200,0.86), 0 3px 6px 0 rgba(255,255,255,0.77); cursor: pointer;
}
.bg-inverse { background-color: #000; color : lightgreen; padding: 0.5rem;  }
.bg-tranparent-box {  background-color: rgba(0,0,0, 0.56)  }

.ow {
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}